import uniIcon from "@/components/m-icon/m-icon.vue";

export default {
	components: {
		uniIcon
	},


	data() {
		return {
			tabCurrentIndex: 0,
			status: 0,
			navList: [{
					state: 0,
					text: '全部',
					orderList: []
				},
				{
					state: 1,
					text: '待上传',
					orderList: []
				},
				{
					state: 2,
					text: '待结算',
					orderList: []
				},
				{
					state: 3,
					text: '已完成',
					orderList: []
				}
			]
		};
	},

	onLoad(options) {
		// 页面显示是默认选中第一个
		this.tabCurrentIndex = 0;
	},

	methods: {
		changeTab(e) {
			this.tabCurrentIndex = e.target.current;
		},
		//顶部tab点击
		tabClick(index) {
			this.tabCurrentIndex = index;
		},
		show() {
			// var aa=document.getElementById("select");
			// if(aa.style.display="none"){
			// 	aa.style.display="block";
			// }else{
			// 	aa.style.display="none";
			// }
			if (this.status == 1) {
				this.status = 0
			} else {
				this.status = 1
			}
		}
	},

};
